import Button from "@/components/elements/Button";
import { UserType } from "@/types/common";
import React, { useState } from "react";
import styles from "../modalForm.module.scss";

type Props = {
  onCloseClick: () => void;
  onLoginClick: (arg: UserType) => void;
  onRegisterClick: (arg: UserType) => void;
};

const AuthForm: React.FC<Props> = ({
  onCloseClick,
  onLoginClick,
  onRegisterClick,
}) => {
  const [registerMode, setRegisterMode] = useState(false);
  const [userEmail, setUserEmail] = useState("");
  const [userPassword, setUserPassword] = useState("");
  const [passwordRepeat, setPasswordRepeat] = useState("");
  const [error, setError] = useState("");

  const onInputChange =
    (type: string) => (e: React.ChangeEvent<HTMLInputElement>) => {
      const value = e.target.value;
      switch (type) {
        case "email":
          setUserEmail(value);
          break;
        case "password":
          setUserPassword(value);
          break;
        case "passwordRepeat":
          setPasswordRepeat(value);
          break;
      }
    };

  const loginClick = () => {
    setError("");
    setRegisterMode(false);
    if (!validateFields()) return;
    onLoginClick({ userEmail, userPassword });
  };

  const registerClick = () => {
    setError("");
    setRegisterMode(true);
    if (!validateFields() || !passwordRepeat) return;
    onRegisterClick({ userEmail, userPassword });
  };

  const closeClick = () => {
    clearForm();
    onCloseClick();
  };

  const validateFields = () => {
    if (!userEmail || !userEmail.match(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/)) {
      setError("email");
      return false;
    }
    if (!userPassword || userPassword.length < 8) {
      setError("password");
      return false;
    }
    if (registerMode && userPassword !== passwordRepeat) {
      setError("passwordRepeat");
      return false;
    }
    return true;
  };

  const clearForm = () => {
    setRegisterMode(false);
    setUserEmail("");
    setUserPassword("");
    setPasswordRepeat("");
    setError("");
  };

  return (
    <div className={styles.modalForm}>
      <Button
        title={"close"}
        className={styles.closeBtn}
        icon="icon_close"
        onClick={closeClick}
      />
      <div className={styles.fieldsWrapper}>
        <label>
          Email
          <input
            autoFocus={true}
            className={error === "email" ? 'error' : ""}
            value={userEmail}
            type="email"
            placeholder="email"
            onChange={onInputChange("email")}
          />
        </label>

        <label>
          Password
          <input
            className={error === "password" ? 'error' : ""}
            value={userPassword}
            type="password"
            placeholder="password (minimum 8 characters)"
            onChange={onInputChange("password")}
          />
        </label>

        {registerMode ? (
          <label>
            Password repeat
            <input
              className={error === "passwordRepeat" ? 'error' : ""}
              value={passwordRepeat}
              type="password"
              placeholder="password repeat"
              onChange={onInputChange("passwordRepeat")}
            />
          </label>
        ) : null}
      </div>
      <div className={styles.buttonsWrapper}>
        <Button text="Login" onClick={loginClick} />
        <Button text="Register" onClick={registerClick} />
      </div>
    </div>
  );
};

export default AuthForm;
